import React, {Component} from 'react';
import styles from "./Home.module.scss";
import Hello from "./Hello/Hello";
import Navigation from "./Navigation/Navigation";
import Introduction from "./Introduction/Introduction";
import Show from "./Show/Show";
import image1 from "../../images/home/coronavirus-4937226_1920.jpg";
import image2 from "../../images/home/workplace-2303851_1920.jpg";
import image3 from "../../images/home/wall-3372631_1920.jpg";

class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {
            count: 1
        };
        this.bgImage = React.createRef();
    }

    render() {
        const {count} = this.state;
        return (
            <div id={styles.home}>
                {/*背景图*/}
                {/*<div className={styles.backGround}/>*/}
                <div ref={this.bgImage} className={styles.backGround}/>
                {/*<div className={styles.backGround}/>*/}
                {/*背景遮罩*/}
                <div className={styles.mask}></div>
                {/*导航*/}
                <Navigation
                    _changePage={(page) => this._changePage(page)}
                    count={count}
                />
                {/*欢迎页*/}
                {count === 1 ? <Hello/> : ""}
                {/*自我介绍页*/}
                {count === 2 ? <Introduction/> : ""}
                {/*展示页*/}
                {count === 3 ? <Show/> : ""}
            </div>
        );
    }

    async _changePage(page) {
        await this.setState({
            count: page
        });

        let bgImage = this.bgImage.current;
        switch (page) {
            case 1 :
                bgImage.style.backgroundImage = `url(${image1})`;
                break;
            case 2 :
                bgImage.style.backgroundImage = `url(${image2})`;
                break;
            case 3:
                bgImage.style.backgroundImage = `url(${image3})`;
                break;
            default:
                break;
        }

    }
}

export default Header;