import React, {Component} from 'react';
import styles from "./Introduction.module.scss"

class Introduction extends Component {
    render() {
        return (
            <div id={styles.introduction}>
                <div className={styles.header}>
                    <h1>您好，</h1>
                    <h1>我是Hatsune，</h1>
                    <h1>一个喜爱编程的前端自学者。</h1>
                </div>
            </div>
        );
    }
}

export default Introduction;